.cart {
}
.cart > .title {
  text-align: center;
  height: 20px;
  line-height: 20px;
  padding: 8px;
  color: #ff5000;
  background-color: #fff;
}
.cart .list {
  height: 120px;
}
.cart .box {
  width: 100%;
  margin-bottom: 9px;
  border-radius: 6px;
  display: flex;
}
.cart .box img {
  width: 30%;
  height: 100%;
}
.cart .box .info {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
}
.cart .box .info .title {
  margin: 15px 0px 5px 15px;
  text-align: left;
  color: #333333;
}
.cart .box .info .price {
  margin: 5px 12px 0px 12px;
  padding-bottom: 5px;
  color: #ff5000;
}
.cart .box .close {
  position: absolute;
  right: 0px;
  padding: 10px;
  color: #999999;
}
