.login {
}
.login .title {
  width: 100%;
  height: 52px;
  background: rgb(238, 238, 238);
  line-height: 52px;
  text-align: left;
  box-sizing: border-box;
  padding-left: 20px;
  position: absolute;
  left: 0px;
  top: 0px;
  font-size: 16px;
  font-weight: bold;
  color: rgb(51, 51, 51);
}
.login .input {
  border-bottom: 1px solid #ff5000;
  padding: 20px 0 0;
}
.login .submit {
  background: -webkit-linear-gradient(left, #ff9000, #ff5000) no-repeat;
  color: #fff;
  box-shadow: 0 2px 4px #f7c7b1;
  height: 45px;
  border-radius: 35px;
  margin-top: 15px;
  font-size: 16px;
}
.login .container {
  padding: 0 20px 20px 20px;
  background-color: #fff;
}
.login .logo {
  background: url(https://gw.alicdn.com/tfs/TB1p.BGQXXXXXbFXFXXXXXXXXXX-160-160.png) no-repeat;
  background-size: contain;
  width: 80px;
  height: 80px;
  margin: 50px auto 15px;
}
