.detail {
}
.detail .img {
  background-repeat: no-repeat;
  background-size: cover;
  height: 46%;
}
.detail .img img {
  width: 100%;
}
.detail .price {
  color: #ff5000;
  font-size: 25px;
  padding-top: 8px;
  padding-bottom: 5px;
}
.detail .price:before {
  content: '\FFE5';
  font-size: 16px;
  margin-left: -1px;
}

.detail .title {
  font-weight: bold;
}

.detail .info {
  padding: 0 15px;
  background-color: #fff;
}
.detail .subinfo {
  padding: 10px 0px;
  display: flex;
  justify-content: space-between;
  color: #999999;
}
.detail .subinfo div {
  width: 33%;
}
.detail .gap {
  width: 100%;
  height: 10px;
  background-color: #f8f8f8;
}
.detail .back {
  position: absolute;
  top: 15px;
  left: 15px;
  color: #fff;
  background-color: #00000066;
  width: 32px;
  height: 32px;
  border-radius: 100%;
}
.detail .back-icon {
  font-size: 26px;
  line-height: 28.9px;
  padding-left: 4.3px;
}
.detail .addTo {
  height: 40px;
  border: 0px;
  margin-top: 5px;
  width: 50%;
  border-radius: 1rem 0px 0px 1rem;
  background-image: linear-gradient(to right, rgb(255, 197, 0), rgb(255, 148, 2));
  float: left;
  padding: 6px 16px;
  color: rgb(255, 255, 255);
  font-weight: bold;
}
.detail .buyNow {
  height: 40px;
  border: 0px;
  margin-top: 5px;
  width: 50%;
  border-radius: 0px 1rem 1rem 0px;
  background-image: linear-gradient(to right, rgb(255, 122, 0), rgb(254, 86, 10));
  padding: 6px 16px;
  color: rgb(255, 255, 255);
  font-weight: bold;
}
.detail .bottom {
  bottom: 0px;
  position: fixed;
  width: 100%;
  display: flex;
  background-color: rgb(255, 255, 255);
}
.detail .bottom .left {
  width: 44%;
  color: #949494;
  font-size: 10px;
  align-items: center;
}
.detail .bottom .right {
  width: 56%;
  padding-right: 5px;
}
