.list-container {
  display: flex;
  flex-wrap: wrap;
  padding: 0 7.5px;
  background: #f2f2f2;
  padding-bottom: 12px;
  margin-bottom: 60px;
}
.list-box {
  width: 47%;
  margin-top: 9px;
  border-radius: 6px;
  overflow: hidden;
  margin-left: 4.5px;
  margin-right: 4.5px;
  background-color: #fff;
}

.list-box img {
  width: 100%;
}
.list-box .info {
  height: 64px;
  padding-left: 10px;
  padding-right: 9px;
}
.list-box .title {
  margin-top: 12;
  height: 17px;
  position: relative;
  overflow: hidden;
  line-height: 18.5px;
  text-overflow: ellipsis;
  min-height: 0;
}
.list-box .price {
  flex-direction: row;
  margin-top: 11px;
  height: 20px;
  line-height: 20px;
  color: #ff5500;
}
